body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 44px;
    font-weight: bold;
    height: 60px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 60px;
  }
  
.square:focus {
    outline: none;
  }
.board-row:after {
    clear: both;
    content: "";
    display: table;
}
.blue {
    height: 55px;
    width: 55px;
  }
  
  .container {
    display: flex;
    flex-direction: row;
  }
  
  .game {
    display: flex;
    flex-direction: column;
    
  }
  
  .game-button {
    box-shadow:inset 0px 1px 3px 0px #91b8b3;
