      color:#ffffff;
      font-family:Arial;
      font-size:20px;
      font-weight:bold;
      padding:28px 72px;
      text-decoration:none;
    text-shadow:0px -1px 0px #2b665e;
    margin-right: 20px;
  }
  
  .red {
    height: 55px;
    width: 55px;
  }
  
  
  .savedGames {
    align-self: flex-start;
    background-color: #EEEEDD;
    display: flex;
    flex-direction: column;
    min-width: 35%;
    min-height: 55vh;
    margin-top: 6%;
    margin-right: 5%;
    margin-left: -30%;
    border: 1px solid;
  }
  
  section {
    display:flex;
    flex-direction: row;
    margin-top: 20px;
  }
  
  .status {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
  }
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
